html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
}
body.auth {
  background-image: url("../img/login_bg.jpg");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.contenedor-sm {
  max-width: 70rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Roboto", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 300;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 2rem;
  line-height: 1.8;
  font-weight: 100;
}

.descripcion-pagina {
  text-align: center;
}

.tagline {
  font-family: "Roboto", sans-serif;
  text-align: center;
  font-size: 3rem;
}

/** Media Queries **/
.formulario {
  margin-top: 5rem;
}
.formulario .campo {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
}
.formulario .campo label {
  font-size: 2rem;
  margin-right: 1rem;
  font-weight: 100;
}
.formulario .campo input {
  width: 30rem;
  flex: 1;
  border-radius: 2rem;
  padding: 1rem;
  border: none;
  background-color: #d8dadf;
}

.acciones {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .acciones {
    display: flex;
    justify-content: space-between;
  }
}
.acciones a {
  display: block;
  text-align: center;
  margin-bottom: 1rem;
  color: #000;
}
.acciones a:last-of-type {
  margin-bottom: 0;
}

.alerta {
  background-color: saddlebrown;
  color: #FFFFFF;
  border-radius: 3rem;
  padding: 1rem;
  margin: 1.6rem auto;
  text-align: center;
}
.alerta.error {
  background-color: #ff4343;
  box-shadow: 0.5rem 0.5rem #a90000;
}
.alerta.exito {
  background-color: #12ff12;
  box-shadow: 0.5rem 0.5rem #007800;
}

.login {
  margin-top: 5rem;
}
.login .uptask {
  text-align: center;
  font-family: "Roboto", sans-serif;
  background: linear-gradient(to right, #0891B2 0%, #5bd9f8 100%);
  color: transparent;
  background-clip: text;
  font-size: 7rem;
}
@media (min-width: 768px) {
  .login .uptask {
    font-size: 10rem;
  }
}
.login input[type=submit] {
  background-color: #0891B2;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 0 auto;
  border-radius: 3rem;
}
@media (min-width: 768px) {
  .login input[type=submit] {
    width: auto;
  }
}
.login input[type=submit]:hover {
  background-color: #066981;
  cursor: pointer;
}
.login .acciones a {
  color: #0891B2;
}

