.barra-mobile {
  border-radius: 1rem;
  background-color: #4338CA;
  padding: 2rem 4rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 768px) {
  .barra-mobile {
    display: none;
  }
}
.barra-mobile h1 {
  color: #FFFFFF;
  margin: 0;
}
.barra-mobile .menu img {
  width: 0.8rem;
}

.barra {
  display: none;
}
@media (min-width: 768px) {
  .barra {
    background-color: #FFFFFF;
    width: 100%;
    height: 8rem;
    border-radius: 2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem;
    box-shadow: 0.2rem 0.2rem 3rem -1.8rem #000;
  }
  .barra p {
    font-weight: 100;
    margin: 0;
  }
  .barra p span {
    font-weight: 500;
    color: #4338CA;
  }
  .barra p span:hover {
    cursor: pointer;
  }
  .barra h2 {
    margin: 0;
    color: #000;
  }
  .barra a {
    color: #000;
    font-size: 4rem;
    display: flex;
    align-items: center;
  }
}

.sidebar {
  opacity: 0;
  background-color: #4338CA;
  height: 0;
  overflow: hidden;
  transition-property: opacity;
  transition-duration: 500ms;
}
.sidebar.mostrar {
  padding: 2rem;
  opacity: 1;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.9);
}
@media (min-width: 768px) {
  .sidebar.mostrar {
    display: none;
  }
}
.sidebar.ocultar {
  opacity: 0;
}
@media (min-width: 768px) {
  .sidebar {
    height: 100%;
    opacity: 1;
    padding: 2rem;
    background-color: #4338CA;
  }
}
.sidebar .contenedor-sidebar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.sidebar .contenedor-sidebar h2 {
  font-weight: 900;
  color: #FFFFFF;
  text-align: center;
}
.sidebar .contenedor-sidebar .cerrar-menu {
  display: flex;
  align-items: center;
  height: 2rem;
  width: 2rem;
  cursor: pointer;
}
@media (min-width: 768px) {
  .sidebar .contenedor-sidebar .cerrar-menu {
    display: none;
  }
}
.sidebar .cerrar-sesion {
  background-color: #4338CA;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 0 auto;
  border-radius: 3rem;
}
@media (min-width: 768px) {
  .sidebar .cerrar-sesion {
    width: auto;
  }
}
.sidebar .cerrar-sesion:hover {
  background-color: #352ca3;
  cursor: pointer;
}
.sidebar a {
  text-align: center;
  display: block;
  font-size: 3rem;
  font-weight: 300;
  text-decoration: none;
  color: #FFFFFF;
  margin-bottom: 3rem;
  padding: 1rem;
}
.sidebar a:hover, .sidebar a.activo {
  color: #4338CA;
  background-color: #FFFFFF;
  border-radius: 2rem;
}

.proyectos {
  margin-top: 3rem;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .proyectos {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .proyectos {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.proyectos .proyecto {
  background: linear-gradient(to right, #8e2de2, #4a00e0);
  border-radius: 2rem;
  padding: 2rem;
  height: 15rem;
  text-decoration: none;
  color: #FFFFFF;
  font-size: 3rem;
  font-weight: 100;
}

