}

.listado-tareas {
  list-style: none;
  padding: 0;
  margin: 5rem 0;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
.listado-tareas .no-tareas {
  text-align: center;
  font-size: 3rem;
  font-weight: 300;
}
.listado-tareas .tarea {
  padding: 0.5rem 2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.listado-tareas .tarea p {
  font-size: 3rem;
  cursor: pointer;
  user-select: none;
}
.listado-tareas .tarea .opciones {
  display: flex;
  gap: 1rem;
}
.listado-tareas .tarea .opciones button {
  border: none;
  text-transform: uppercase;
