  left: 0;
  right: 0;
  bottom: 0;
}
.modal .formulario {
  max-width: 60rem;
  border-radius: 2rem;
  background-color: #FFFFFF;
  padding: 2rem;
  margin: 0 auto;
  transform: translateY(-30rem);
  transition: all 1s;
  opacity: 0;
}
.modal .formulario legend {
  font-size: 2rem;
