@use 'base/mixins' as m;
@use 'base/variables' as v;

.crear {
    margin-top: v.$separacion;
    .uptask {
        @include m.textoDegradado(v.$morado);
    }

    input[type="submit"] {
        @include m.boton(v.$morado,v.$blanco);
    }

    .acciones {
        a {
            color: v.$morado;
        }
    }
}